@use "base/variables" as v;
@use "base/mixins" as m;

.navegacion {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 100%;
  @include m.desktop {
    width: auto;
    flex-direction: row;
  }
  &__link {
    font-size: 2rem;
    font-weight: 700;
    color: v.$negro;
    margin: 1rem 0;
    @include m.desktop {
      margin: 0 2rem;
      &:last-of-type {
        margin-right: 0;
      }
    }
    &--registrar {
      @include m.boton(v.$primario, v.$blanco);
    }
  }
}
